import * as React from "react"
import "./toc.css"


const Toc = (data) => {
    const tochtml = data.data
    if (tochtml === "") {
        return (<></>)
    }
    return (
        <nav className="tocnav">
            <span>Table of Contents</span>
            <div dangerouslySetInnerHTML={{ __html: tochtml }}/>
        </nav>
    )
}

export default Toc