
import * as React from "react"
import { useState, useEffect } from "react"
import { Link } from "gatsby"
import "./nav.css"
import Toggle from "./themetog"


const Nav = ({title, date, categories, count, location}) => {
  const centerTitle = title ? title : <></>
  const centerDate = date ? date : <></>
  const categrs = categories ? categories : <></>
  const tcount = count ? count : 0
  const isRoot = location === "/" ? "active" : "non-active"
  const dropdown = location === "/" ? <div><Link to="/" className="navmenu" itemProp="url">메뉴</Link></div> : <></>
  // 햄버거 메뉴 함수
  const [hamopen, sethamopen] = useState(false)
  const openHam = () => {
    if (hamopen) {
      document.getElementById("body").style.overflow = "scroll";
      document.getElementById("body").style.paddingRight = "0px";

      document.getElementById("ham-shadow").style.visibility = "hidden"
      document.getElementById("ham-shadow").style.opacity = "0"

      document.getElementById("ham-menu").style.transform = "translateX(-100%)"

      sethamopen(false);
    }
    else {
      if (window.innerWidth - document.body.clientWidth > 15) {
        document.getElementById("body").style.paddingRight = "17px";
      }
      document.getElementById("body").style.overflow = "hidden";

      document.getElementById("ham-shadow").style.visibility = "visible"
      document.getElementById("ham-shadow").style.opacity = "0.5"

      document.getElementById("ham-menu").style.transform = "translateX(0%)"

      sethamopen(true);
    }
  }
  // 새로고침 및 페이지 이동 시 초기화
  useEffect(() => {
    document.getElementById("nav").style.top = "0px";
    return () => {
      document.getElementById("nav").style.top = "0px";
    }
  }, []);

  // (auto-hiding nav) and (title in nav)
  if (typeof window !== 'undefined') {
      let prevScrollpos = window.pageYOffset;
      window.onscroll = function () {
        const maxScroll = document.body.clientHeight - window.innerHeight;
        let currentScrollPos = window.pageYOffset;
        let titleopacity = window.pageYOffset < 200 ? 0 : 1;
        document.getElementById("navlocation").style.opacity = `${titleopacity}`;
        if (
            (maxScroll > 0 && prevScrollpos > currentScrollPos && prevScrollpos <= maxScroll) 
          || (maxScroll <= 0 && prevScrollpos > currentScrollPos)
          || (prevScrollpos <= 0 && currentScrollPos <= 0)
          ) {
          document.getElementById("nav").style.top = "0px";
        } else {
          document.getElementById("nav").style.top = "-63px";
        }
        prevScrollpos = currentScrollPos;
      }
    }
  return(
      <div id="nav" className="navmaster">
        <div htmlFor="ham-check" id="ham-label" onClick={openHam}>
          <span/><span/><span/>
        </div>
        <div id="ham-shadow" onClick={openHam}/>
        <div id="ham-menu">
            <ol>
                <li key="category_All"><Link to="/" className={isRoot}>All({count})</Link></li>
                {categrs.map(cate => {
                    const cn = cate.fieldValue === location ? "active" : "non-active"
                    return (
                        <li key={cate.fieldValue}><Link to="" className={cn}>{cate.fieldValue}({cate.totalCount})</Link></li>
                    )
                })}
            </ol>
        </div>
        <Link to="/" className="navlogo" itemProp="url">ljho01</Link>
        {dropdown}
        <div id="navlocation">
          <span className="navtitle">{centerTitle}</span>
          <span className="navdate">{centerDate}</span>
        </div>
        <Toggle/>
        <div id="navline" className="navline"/>
      </div>
  )
}

export default Nav




